/* Styling for SearchInput component */

/* Container styling */
.search-container {
    display: flex;
    align-items: center;
    float: right;
    margin-right: 2rem;
  }
  
  /* Input styling */
  .search-input {
    width: 400px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
    outline: none;
  }

  @media screen and (max-width: 1050px) {
    .search-input {
      width: 90%;
    }
  }
  