.ts-box {
    margin: 20px;
    min-height: 15rem;
    overflow: hidden;
}

.ts-container {
    display: flex;
    overflow: hidden;
}

.ts-container .card {
    margin-right: 5px;
    min-width: 14rem;
    min-height: 24rem;
    padding: auto;
    align-items: center;
    justify-content: center;
}

.ts-container .card img {
    height: 17rem;
    width: 8rem;
}

.card-body .card-text h6 {
    font-weight: bold;
}

@media screen and (max-width: 1050px) {
    .ts-container {
        overflow-x: auto;
    }
}