.category-section {
    margin: 2rem 1.5rem;
    padding: 1rem;
  }
  
  .category-container {
    display: flex;
  }
  
  .category-item {
    margin-right: 3.2rem;
  }
  
  .category-item img {
    width: 60px; 
    height: 60px;
  }

  .category-item .category-button {
    padding: 12px;
  }
  
  .dropdown-wrapper:hover .dropdown-menu {
    display: block;
  }
  

  .category-item .dropdown-toggle {
    margin-top: 5px; 
  }

  .dropdown-wrapper {
    display: inline-block; 
  }
  
  .dropdown-wrapper:hover > .dropdown-menu {
    display: block; 
  }
  
  @media screen and (max-width: 1000px) {
    .category-container {
        overflow-x: auto;
        overflow-y: auto;
      }
  }