.footer-box {
    background-color: black;
    min-height: 40vh;
    padding: 40px;
    color: white;
    font-size: 12px;
    overflow: hidden;
}

.footer-container {
    min-height: 20vh;
    overflow: hidden;
    display: flex;
}

.footer-container .footer-section1 {
    width: 55%;
    display: flex;
}

.footer-section1 .sub-section {
    margin-right: 60px;
}

.footer-container .footer-section2 {
    width: 40%;
    float: left;
}

.footer-section.sub-section {
    margin: 2rem 0;
}

.footer-section1 .sub-section ul {
    list-style-type: none;
}

.footer-section2 .sub-section h6 {
    font-size: 16px;
    color: rgb(148, 144, 144);
}

.footer-section .sub-section li {
    font-weight: bold;
    color: white;
}

.footer-section {
    overflow: hidden;
}

.footer-section2 {
    display: flex;
}

.footer-section2 .address-section {
    width: 50%;
    padding: 0 10px;
}

@media screen and (max-width: 1050px) {
    .footer-box {
        padding: 15px;
    }

    .footer-container {
        flex-direction: column;
    }

    .footer-section2 .section1 {
        margin-left: 20px;
        margin-top: 20px;
        min-width: 150px;
    }
    .footer-section2 .section2 {
        margin-left: 10px;
        margin-top: 20px;
        width: 220px;
    }

    .footer-section1 .sub-section {
        margin-right: 2px;
    }
}
